.card {
border: 2px red solid;
border-radius: 2px;
width: 165px;
height: 165px;
margin: 2rem;
transition: all .2s ease-in-out;
position: relative;
box-shadow: 6px 6px 6px #999;
}

.card > .img-container {
height: 100%;
overflow: hidden;
background:rgb(255, 255, 255);
}

.card > .img-container > img {
height: 100%;
}

.card:hover {
opacity: 0.8;
box-shadow: 8px 8px 20px #999;
transform: scale(1.1);
}

.card:active {
transform: translate(1px, 1px);
}